<template>
<section class="bg-alt hero p-0">
	<div class="container-fluid">
		<div class="row ">
			<div class=" col-md-4 bg-white text-dark py-5 text-center layui-side border border-secondary" id="right " style="position:fixed">
			
        <h1 class=" h2">基于日志分析的服务适配优化</h1>
        <hr/>

        <div class="pt-2 mt-5 mb-5 d-flex flexItem">
          <button  class="btn btn-primary btn-lg " id='Random' @click="Random" type="primary" round :disabled="this.RandomDisabled ==-1">
            FCFS &emsp;算法
          </button>
          <button  class="btn btn-success btn-lg" id='NSGA' @click="NSGA" type="warning" round :disabled="this.NSGADisabled ==-1">
            NSGA-II 算法
          </button>
        </div>

        <!-- 间隔 -->
        <div class="pt-2 mb-5"></div>

        <el-table
                            :header-cell-style="{

                                fontSize: '18px',
                               
                                'text-align': 'center'
                            }"

        :data="tableData"
        height="400"
        border
        style="width: 100%"
        >
        <el-table-column
          prop="TraceId"
          label="服务请求ID"
          width="120"
          >
        </el-table-column>
        <el-table-column
          prop="Activity"
          label="子服务"
          width="100"
          >
        </el-table-column>
        <el-table-column
          prop="State"
          label="服务状态"
          width="100">
        </el-table-column>
        <el-table-column 
          prop="TimeStamp"
          label="时间戳"
          >
        </el-table-column>
      </el-table>
			</div>

      <!-- 右侧 -->
			<div class="col-md-7 ml-auto px-0 py-2">

				<!--算法性能对比-->
				<section class="bg-alt border border-secondary mb-5" style="width: 900px; ">
          <h1 class=" h4 m-3">算法性能对比 </h1>              
            <div class="container-fluid  mb-5  border-secondary">
              <div class="row d-md-flex  ">    
                <div class="chart ">   
                  <div id="awardsdata" :style="{width:'400px',height:'300px'}"></div>
                </div>
                              
                <div class="chart1 ">  
                  <div id="awardsdata1" :style="{width:'400px',height:'300px'}"></div>
                </div>
              </div>
            </div>

				</section>

        <!-- 间隔 -->
        <div style="height:20px;"></div>

        <!-- 日志模型显示 -->
        <section class="bg-alt border border-secondary mb-5 mt-5" style="width: 900px; ">
          <h1 class=" h4 m-3">日志模型显示 </h1>  
            <div class="container-fluid  border-secondary ">
              <div class="row d-md-flex  ">

                <img src="../assets/BPMmining/imgs/2.png" alt="Male" class=" img-fluid "/>
              </div>
            </div>

        </section>

                <!-- 间隔 -->
        <div style="height:20px;"></div>

				<!--算法演示过程-->
				<section class="bg-alt  mt-5  bg border border-secondary mb-5 mt-5">
					<h1 class=" h4 m-3">算法演示过程 </h1>  
          <div class="container-fluid  border-secondary ">
						<div class="row d-md-flex  text-primary rizhi text-dark  "  id="bottom" >

                      <div class="cloud" >
                        <el-image :src="require('@/assets/BPMmining/imgs/Cloud.png')" ></el-image>
                      </div>
                      <div class="edge1" >
                        <el-image :src="require('@/assets/BPMmining/imgs/Edge.png')" ></el-image>
                      </div>
                      <div class="edge2" >
                        <el-image :src="require('@/assets/BPMmining/imgs/Edge.png')" ></el-image>
                      </div>
                      <div class="edge3" >
                        <el-image :src="require('@/assets/BPMmining/imgs/Edge.png')" ></el-image>
                      </div>
						</div>
					</div>
				</section>
			</div>
		</div>
	</div>
</section>
</template>


  


<script src="http://ajax.aspnetcdn.com/ajax/jQuery/jquery-1.8.0.js"></script>
<script>
import $ from 'jquery';
import axios from 'axios';
let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");

export default {
  

  data() {
      return {
        timer:'',
        items: [
          { type: '', label: '读取文件日志显示' }
        ],
        RandomData:null,
        NSGAData:null,
        RandomDisabled:0,
        NSGADisabled:0,
        OptDisabled:0,
        tableData: null,
        hasRandom:0,
        hasNSGA:0,
        randomErgCon:null,
        randomDelayCon:null,
        nsgaErgCon:null,
        nsgaDelayCon:null,
        chart1Option:{
        tooltip: {
          trigger: "axis",
        },
        grid: {
          left: '20%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: [ "FCFS算法","NSGA-II 算法"],
            axisLine: {
              symbol : ['none', 'arrow'],
            },    
            axisTick: {
              show: false
            },
            axisLabel:{
              interval:0,
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          },
        ],
        yAxis: [
          {
            type: "value" ,
            name:"能量消耗/J",
            nameTextStyle: {
            fontSize: 22,color:"rgba(42, 42, 42, 1)",
            },
            splitLine:{show: false}, 
            axisLine: {
              show:true,
              symbol : ['none', 'arrow'],
            },
            axisLabel:{         
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          }
       ],
        series: [
          {
            type: "bar",
            data: [this.randomErgCon,this.nsgaErgCon],
            barWidth: 25,
            label: {
              fontSize: 200
            }
          },
        ],
      },
      chart2Option:{
        tooltip: {
          trigger: "axis",
        },
        grid: 
        {
        left: '20%',
        right: '4%',
        bottom: '3%',
        containLabel: true
        },
        legend: {
          x: "500px",
          y: "480px",
          data: [ "FCFS算法","NSGA-II算法"],
        },
        xAxis: [
          {
            type: "category",
            data:["FCFS算法","NSGA-II算法"],
            axisLine: {
              symbol : ['none', 'arrow']
            },   
            axisTick: {
              show: false
            },
            axisLabel:{
              interval:0,
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            }
          },
        ],
        yAxis: [
          {
            type: "value" ,
            name:"时间延迟/s",
            nameTextStyle: {
              fontSize: 22,color:"rgba(42, 42, 42, 1)",
            },
            splitLine:{show: false}, 
            axisLine: {
              show:true,
              symbol : ['none', 'arrow'],
            },
            axisLabel:{
              fontSize:22,
              color:"rgba(42, 42, 42, 1)",
            },
          }
       ],
        series: [
          {
            type: "bar",
            data: null,
            barWidth: 25,
            label: {
              fontSize: 200
            }
          },
        ],
      }
      }
    },
    methods:{
    chart() {
      let chart = echarts.init(document.getElementById("awardsdata"));
    },
    option() {
      let chart = echarts.init(document.getElementById("awardsdata1"));
    },
      getLog() {
        axios
        .get('/getLog')
        .then(response => {
          this.tableData=response.data.data;
        });
      },
      getRandom(data){
        var list=["Triage","Register","Check","X-ray","Visit","FinalVisit","Prepare","OrganizeAmbulance"]
        var left=[200,50,250,450];
        var top=[80,350,350,350];
        var traceCount=0;
        $("#bottom").children().filter(".trace").each(function(){
          if($(this).data("nodeid")==data.nodeid){
            traceCount++;
          }
        });
        let a = document.getElementById("bottom");
        let div = document.createElement("div");
        let img = document.createElement("img");
        img.setAttribute("src", require("@/assets/BPMmining/imgs/"+list[data.id]+".png"));
        img.style.display="block";
        div.style.left = left[data.nodeid]+traceCount*20+'px';
        div.style.top = top[data.nodeid]+traceCount*20+'px';
        div.style.border="1% solid black";
        div.style.borderRadius="10%";
        div.append(img);
        div.style.position="absolute";
        div.style.zIndex = 2;
        div.className="trace";
        div.dataset.nodeid = data.nodeid;
        a.appendChild(div);
      },
      Random(){   
        $("#bottom").children().filter(".trace").each(function(){
          $(this).remove();
        });
        document.getElementById('Random').disabled
        this.RandomDisabled=-1
        this.NSGADisabled=-1
        this.OptDisabled=0
        this.hasRandom=1
        axios
        .get('/getRANDOM')
        .then(response => {
          var bestServiceRequest=response.data.data.bestServiceRequest;
          var bestServiceList=response.data.data.bestServiceList;
          var count=0
          let timer = setInterval(()=>{
            if(count<bestServiceRequest.length){
              this.getRandom(bestServiceList[bestServiceRequest[count]]);
            }
            else{
              clearInterval(timer)
              this.RandomDisabled=0
              this.NSGADisabled=0
              if(this.hasRandom&&this.hasNSGA){
                this.OptDisabled=1
              }  
            }
            count++;
          },1000)
            this.randomErgCon = response.data.data.bestErgCon;
            this.randomDelayCon = response.data.data.bestDelayCon;
            
            let chart1 = echarts.init(document.getElementById("awardsdata"));
            this.chart1Option.series[0].data= [this.randomErgCon,this.nsgaErgCon];
            chart1.setOption(this.chart1Option);
            let chart2 = echarts.init(document.getElementById("awardsdata1"));
            this.chart2Option.series[0].data= [this.randomDelayCon,this.nsgaDelayCon];
            chart2.setOption(this.chart2Option);
        });   
          
      },
      getNSGA(data){
        var list=["Triage","Register","Check","X-ray","Visit","FinalVisit","Prepare","OrganizeAmbulance"]
        var left=[200,50,250,450];
        var top=[80,350,350,350];
        var traceCount=0;
        $("#bottom").children().filter(".trace").each(function(){
          if($(this).data("nodeid")==data.nodeid){
            traceCount++;
          }
        });
        let a = document.getElementById("bottom");
        let div = document.createElement("div");
        let img = document.createElement("img");
        img.style.display="block";
        img.setAttribute("src", require("@/assets/BPMmining/imgs/"+list[data.id]+".png"));
        div.style.left = left[data.nodeid]+traceCount*20+'px';
        div.style.top = top[data.nodeid]+traceCount*20+'px';
        div.append(img);
        div.style.border="1% solid black";
        div.style.borderRadius="10%";
        div.style.position="absolute";
        div.style.zIndex = 2;
        div.className="trace";
        div.dataset.nodeid = data.nodeid;
        a.appendChild(div);
      },
      NSGA(){
        $("#bottom").children().filter(".trace").each(function(){
          $(this).remove();
        });
        this.RandomDisabled=-1
        this.NSGADisabled=-1
        this.OptDisabled=0
        this.hasNSGA=1
        axios
          .get('/getNSGA')
          .then(response => { 
              var bestServiceRequest=response.data.data.bestServiceRequest;
              var bestServiceList=response.data.data.bestServiceList;
              var count=0
              let timer = setInterval(()=>{
                if(count<bestServiceRequest.length){
                  this.getNSGA(bestServiceList[bestServiceRequest[count]]);
                }
                else{
                  clearInterval(timer)
                  this.RandomDisabled=0
                  this.NSGADisabled=0
                  if(this.hasRandom&&this.hasNSGA){
                    this.OptDisabled=-1
                  }  
                }
                count++;
              },1000)
            this.nsgaErgCon = response.data.data.bestErgCon;
            this.nsgaDelayCon = response.data.data.bestDelayCon;
            
            let chart1 = echarts.init(document.getElementById("awardsdata"));
            this.chart1Option.series[0].data= [this.randomErgCon,this.nsgaErgCon];
            chart1.setOption(this.chart1Option);
            let chart2 = echarts.init(document.getElementById("awardsdata1"));
            this.chart2Option.series[0].data= [this.randomDelayCon,this.nsgaDelayCon];
            chart2.setOption(this.chart2Option);    
            }       
          );      
      },

    },
    mounted(){
      this.getLog();
      let chart1 = echarts.init(document.getElementById("awardsdata"));
      let chart2 = echarts.init(document.getElementById("awardsdata1"));
      chart1.setOption(this.chart1Option);
      chart2.setOption(this.chart2Option);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    },
}
</script>

<style >
  .bg {
      background:url("../assets/BPMmining/imgs/background.png") no-repeat center;
      background-size:contain;
      position: relative;
      width: 900px;
      height: 600px;
  }
        .flexItem{
          Justify-content:Space-evenly;
        }
          .layui-side{

            top:0;
            bottom:0;

        }
        .rizhi {
          position: relative;
        }
        .tujie {
          position: absolute;
          bottom: 30px;
          right: -400px;
        }


  .cloud{
      position: absolute;
      left: 150px;
      top:20px; 
      width:205px;
      height:141px;
      z-index: 1;
  }
  .edge1{
      position: absolute;
      left: 20px;
      top:320px; 
      width:150px;
      z-index: 1;
  }
  .edge2{
      position: absolute;
      left: 220px;
      top:320px; 
      width:150px;
      z-index: 1;
  }
  .edge3{
      position: absolute;
      left: 420px;
      top:320px; 
      width:150px;
      z-index: 1;
  }

  </style>